.app {
  padding: 20px;
  font-family: Arial, sans-serif;
  transition: background-color 0.3s, color 0.3s;
}

.dark {
  background-color: #333;
  color: white;
}

.high-contrast {
  background-color: yellow;
  color: black;
}

.controls {
  margin: 10px 0;
}

.todo-container {
  margin-top: 20px;
}

.todo-container h2 {
  margin-bottom: 10px;
}

.todo-container ul {
  list-style-type: none;
  padding: 0;
}

.todo-container li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.timestamp {
  font-size: 0.8em;
  color: gray;
}
