/* Navbar.css */
.navbar {
    position: fixed;
    left: -250px; /* Start hidden */
    top: 0;
    width: 250px;
    height: 100%;
    background: #333;
    color: #fff;
    transition: left 0.3s;
    padding: 20px;
    z-index: 1000;
  }
  
  .navbar.open {
    left: 0; /* Show when open */
  }
  
  .navbar .close-btn {
    background: none;
    border: none;
    color: white;
    font-size: 24px;
    cursor: pointer;
  }
  
  .navbar ul {
    list-style: none;
    padding: 0;
  }
  
  .navbar li {
    margin: 15px 0;
  }
  
  .navbar a {
    color: #fff;
    text-decoration: none;
  }
  